import moment from "moment";
import Post from "./Post";
import { Model } from "./Support/Model";
export default class Thread extends Model {
    constructor() {
        super(...arguments);
        //runtime
        this.unreadPostCount = 0;
    }
}
Thread.route = 'thread';
Thread.exposes = [
    'created_at',
    'id',
    'title',
    'post_count',
];
Thread.transformations = {
    'created_at': (created_at) => moment(created_at),
    'latest_posts': (latest_posts) => latest_posts.reverse(),
};
Thread.relations = {
    'posts': { model: Post, many: true },
    'first_post': { model: Post },
    'latest_posts': { model: Post, many: true },
};
