import { writable } from "svelte/store";
class Popup {
    constructor() {
        this.open = false;
        this.mode = 'center';
        this.component = null;
    }
}
export const popup = writable(new Popup());
export const closePopup = () => popup.update(p => { p.open = false; return p; });
export const setPopup = (toRender, mode = 'center') => popup.update(p => {
    p.component = toRender;
    p.open = true;
    p.mode = mode;
    return p;
});
