import axios from "axios";
import Cookies from "js-cookie";
import env from "./env.js";
const config = {
    baseURL: env.basepath,
    headers: {
        'Content-Type': 'application/json',
    },
};
const api = axios.create(config);
export default {
    listBoards: () => api.get('/api/internal/v1/list/boards'),
    createThread: (data) => fetch('/api/internal/v1/create/thread', {
        method: 'POST',
        headers: { accept: 'application/json' },
        body: data
    }),
    createPost: (data) => fetch('/api/internal/v1/create/post', {
        method: 'POST',
        headers: { accept: 'application/json' },
        body: data
    }),
    applyToken: (token) => api.post('/api/internal/v1/token/apply', { token }),
    thread: {
        subscribe: (thread_id, token) => api.post('/api/internal/v1/thread/subscribe/', { thread_id, token: token !== null && token !== void 0 ? token : Cookies.get('LARABA-TOKEN') }),
        unsubscribe: (thread_id, token) => api.post('/api/internal/v1/thread/unsubscribe/', { thread_id, token: token !== null && token !== void 0 ? token : Cookies.get('LARABA-TOKEN') }),
    }
};
